import React, { useState } from 'react';
import { Select, Typography, Row, Col, Avatar, Card } from 'antd';
import moment from 'moment';


import { useGetCryptoNewsQuery } from '../services/cryptoNewsApi';//imported CryptosNews to get add to the amount of cryptos per page
import { useGetCryptosQuery } from '../services/cryptoApi';//imported Cryptos to get an amount of cryptos per page

const { Text, Title } = Typography;
const { Option } = Select;
//demo image if theres no image available
const demoImage = 'https://www.bing.com/th?id=OVFT.mpzuVZnv8dwIMRfQGPbOPC&pid=News';

const News = ({ simplified }) => {
	const [newsCategory, setNewsCategory] = useState('Cryptocurrency');
	const { data: cryptoNews } = useGetCryptoNewsQuery({ newsCategory, count: simplified ? 6 : 12 });
	const { data } = useGetCryptosQuery(100);
//if there no news render 'loading' until info is available
	if (!cryptoNews?.value) return 'loading...';
	//placeholder will render 100 coins
  return (
		<Row gutter={[24, 24]}>
			{!simplified && (
				<Col span={24}>
					<Select showSearch className="select-news" placeholder="Select a Crypto News" optionFilterProp="children" onChange={(value) => setNewsCategory(value)} filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase())}>
						<Option value="Cryptocurrency">Crypto Currency</Option>
						{data?.data?.coins.map((coin) => (
							<Option value={coin.name}> {coin.name}</Option>
						))}
					</Select>
				</Col>
			)}
			{cryptoNews.value.map((news, i) => (
				<Col xs={24} sm={12} lg={8} key={i}>
					<Card hoverable className="news-card">
						<a href={news.url} target="_blank" rel="noreferrer">
							<div className="news-image-container">
								<Title className="news-title" level={4}>
									{news.name}
								</Title>
								<img style={{ maxWidth: '200px', maxHeight: '100px' }} src={news?.image?.thumbnail?.contentUrl || demoImage} alt="news" />
							</div>
							<p>{news.description > 100 ? `${news.description.substring(0, 100)}...` : news.description}</p>
							<div className="provider-container">
								<Avatar src={news.provider[0]?.image?.thumbnail?.contentUrl || demoImage} alt="news" />
								<Text className="provider-name">{news.provider[0]?.name}</Text>
							</div>
							<Text>{moment(news.datePublished).startOf('ss').fromNow()}</Text>
						</a>
					</Card>
				</Col>
			))}
		</Row>
	);
};

export default News;
